import React, { Component } from "react"
import './RadioButton.css'

export default class RadioButton extends Component {

    state = {
        isActive: true
    }

    onClickHandle = () => {
        this.setState({
            isActive: !this.state.isActive
        })
    }
    
    render() {
        return (
            <div className={this.state.isActive ? "radio-button radio-button_active" : "radio-button"} onClick={this.onClickHandle}>
                <div className={this.state.isActive ? "radio-button__handle radio-button__handle_active" : "radio-button__handle"}></div>
                {/* {this.props.buttonTitle && this.props.buttonTitle} */}
            </div>
        )
    }
}