import React, { Component } from "react"
import ReactCSSTransitionGroup from "react-addons-css-transition-group"
import './ModalPopUp.css'
import CloseButton from '../close-button/CloseButton'
import Buttons from '../buttons/Buttons'

export default class ModalPopUp extends Component {
    
    state = {
        isActive: true,
        body: (
            <ReactCSSTransitionGroup
                transitionName = "modal-popup"
                transitionAppear={true}
                transitionAppearTimeout={150}
                transitionEnterTimeout={500}
                transitionLeaveTimeout={300}
            >
                <div className="modal-popup">
                    <div style={{textAlign: "center", marginBottom: 8, marginTop: 8}} className="content__text">Удалить подписчика?</div>
                    <div className="modal-popup__buttons">
                        <Buttons buttonType={"blue-stroke"} text={"Отмена"}/>
                        <Buttons buttonType={"red-solid"} text={"Удалить"}/>
                    </div>
                    <CloseButton pos={"absolute"} top={8} right={8} />
                </div>
            </ReactCSSTransitionGroup>
        )
    }
    handleClick = () => {
        this.setState({ isActive: !this.state.isActive })
    }
    
    render() {
        return (
            <div onClick={this.handleClick}>
                { this.state.isActive && this.state.body }
            </div>
        )
    }
}