import React, { Component } from "react"
import './DropDown.css'

let value = ""

const Item = (props) => {

    const getValue = (e) => {
        return value = e.target.value
    }

    return (
        <div className="dropdown__item" onClick={getValue}>
            <div className="dropdown__input-text">{props.text}</div>
        </div>
    )
}

export default class DropDown extends Component {

    state = {
        isActive: false,
        items: [
            "Пункт меню 1",
            "Пункт меню 2",
            "Пункт меню 4",
            "Пункт меню 5"
        ],
        placeHolder: "Выберите пункт меню"
    }
    itemsData = this.state.items.map( item => {
        return <Item text={item} />
    })

    openDropdownHandle = () => {
        this.setState({ isActive: !this.state.isActive })
    }
    closeDropdownHandle = placeHolder => e => {
        this.setState({ isActive: !this.state.isActive })
        this.setState({ placeHolder: placeHolder })
    }
    
    
    render() {

        const itemList = (
            <div className="dropdown__list-items" onChange={this.closeDropdownHandle('name')}>
                { this.itemsData }
            </div>
        )

        return (
            <div className="dropdown">
                <div className="dropdown__input" onClick={this.openDropdownHandle}>
                    <div className={this.state.isActive ? "dropdown__input-text dropdown__input-text_active" : "dropdown__input-text"}>{this.state.placeHolder}</div>
                    <div className="dropdown__input-icon"></div>
                </div>
                { this.state.isActive ? itemList : null }
                {value}
            </div>
        )
    }
}