import React from "react"
import './StyleGuideSidebarItem.css'

const StyleGuideSidebarItem = props => {
    
    return (
        
        <div 
            id={props.id} 
            className={props.activeItem === props.id ? "style-guide__sidebar__item_active" : "style-guide__sidebar__item"} 
            onClick={() => {
                props.updateData(props.id)
            }}
        >
        {props.title}
        </div>
        
    )
}

export default StyleGuideSidebarItem