import React, {Component} from "react"
import './Input.css'

export default class Input extends Component {

    state = {
        isActive: false,
        value: "",
        placeholder: "Введите что-нибудь"
    }
    
    handleClick = () => {
        this.setState({ isActive: !this.state.isActive })
    }
    
    render() {
        return (
            <div className="input">
                <div className={this.state.value ? "input__title" : "input__title_placeholder"}>{ this.state.isActive ? this.state.value : this.state.placeholder }</div>
            </div>
        )
    }
}