import React, { Component } from "react"
import './Checkbox.css'

export default class Checkbox extends Component {

    state = {
        isActive: true
    }

    onClickHandle = () => {
        this.setState({
            isActive: !this.state.isActive
        })
    }
    
    render() {

        const checkbox = (
            <div className={this.state.isActive ? "checkbox checkbox_active" : "checkbox"} onClick={this.onClickHandle}>
                <div className={this.state.isActive ? "checkbox__handle checkbox__handle_active" : "checkbox__handle"}>
                    <div className={this.state.isActive ? "checkbox__icon_active" : "checkbox__icon"}></div>
                </div>
            </div>
        )
        const checkboxWithTitle = (
            <div className={this.state.isActive ? "checkbox checkbox_active" : "checkbox"} onClick={this.onClickHandle}>
                <div className={this.state.isActive ? "checkbox__handle checkbox__handle_active" : "checkbox__handle"}>
                    <div className={this.state.isActive ? "checkbox__icon_active" : "checkbox__icon"}></div>
                </div>
                <div className="checkbox__title">{this.props.title}</div>
            </div>
        )

        return this.props.title ? checkboxWithTitle : checkbox
    }
}