import React from "react"
import './Badges.css'

const Badges = props => {

    const randomCount = (min, max) => {
        let rand = min + Math.random() * (max + 1 - min)
        rand = Math.floor(rand)
        return rand
    }
    
    return (
        
        <div className={`badges_${props.color}`}>
            <div className="badges__count">{props.count || randomCount(3, 50)}</div>
        </div>
        
    )
}

export default Badges