import React, { Component } from "react"
import './ProfileButton.css'
import ProfilePopUp from '../profile-popup/ProfilePopUp'

export default class ProfileButton extends Component {

    state = {
        photo: '',
        name: ''
    }

    formatName(first, last) {
        let firstName = first[0].toUpperCase() + first.substring(1)
        let lastName = last[0].toUpperCase() + last.substring(1)
        return firstName + ' ' + lastName
    }

    getAvatar() {

        let userData = {}

        let xhr = new XMLHttpRequest()
        xhr.open('GET', 'https://randomuser.me/api/', false)
        xhr.send()

        if (xhr.status !== 200) {
            console.log( xhr.status + ': ' + xhr.statusText )
        } else {
            userData = JSON.parse(xhr.responseText)
            
            this.setState({
                photo: userData.results[0].picture.thumbnail,
                name: this.formatName( userData.results[0].name.first, userData.results[0].name.last )
            })
        }
    }

    componentDidMount() {
        this.getAvatar()
    }
    
    render() {
        return (
            <div className="header__profile-button" onMouseEnter={this.foo}>
                <div className="profile-button__username">{this.state.name}</div>
                <div className="profile-button__avatar">
                    <img width="24" height="24" alt="" src={this.state.photo}/>
                </div>
                <ProfilePopUp />
            </div>
        )
    }
}