import React, { Component } from "react"
import './Menu.css'
import MenuItem from '../menu-item/MenuItem'

export default class Menu extends Component {

    state = {
        isOpen: true,
    }
    showHideHandle = () => {
        this.setState({ isOpen: !this.state.isOpen })
    }
    
    render() {

        const openedMenu = (
            <div className="menu menu_opened">
                <div className="menu__links">
                    <MenuItem isActive={false} icon={"main-icon"} title={"Главная"}/>
                    <MenuItem isActive={true} icon={"mailing-icon"} title={"Рассылки"}/>
                    <MenuItem isActive={false} icon={"users-icon"} title={"Подписчики"}/>
                    <MenuItem isActive={false} icon={"statistics-icon"} title={"Статистика"}/>
                    <MenuItem isActive={false} icon={"news-icon"} title={"Новости"}/>
                </div>
                <div className="menu__hide-button" onClick={this.showHideHandle}></div>
            </div>
        )
        const hiddenMenu = (
            <div className="menu menu_hidden">
                <div className="menu__show-button" onClick={this.showHideHandle}></div>
            </div>
        )

        return (
            this.state.isOpen ? openedMenu : hiddenMenu
        )
    }
}