import React, { Component } from "react"
import './MoneyButton.css'

export default class MoneyButton extends Component {

    randomCount(min, max) {
        let rand = min + Math.random() * (max + 1 - min)
        rand = Math.floor(rand)
        return new Intl.NumberFormat('ru-RU').format(rand)
    }
    
    render() {
        return (
            <div className="header__money-button">
                { this.randomCount(10000, 500000) + " ₽" }
            </div>
        )
    }
}