import React, { Component } from "react"
import './Switcher.css'

export default class Switcher extends Component {

    state = {
        isActive: false
    }
    
    onClickHandle = () => {
        this.setState({
            isActive: !this.state.isActive
        })
    }

    render() {
        return (
            <div className={this.state.isActive ? "switcher switcher_active" : "switcher"} onClick={this.onClickHandle}>
                <div className={this.state.isActive ? "switcher__handle switcher__handle_active" : "switcher__handle"}></div>
            </div>
        )
    }
}