import React from "react"
import './Attach.css'

const Attach = props => {
    
    return (
        
        <div className={ props.isActive ? "attach-button" : "attach-button attach-button_disabled" }>
            <div className={ props.isActive ? "attach-button__icon" : "attach-button__icon attach-button__icon_disabled" }></div>
            <div className={ props.isActive ? "attach-button__title" : "attach-button__title attach-button__title_disabled" }>{props.title || "Прикрепить файл"}</div>
            { props.withFileType && <div className={ props.isActive ? "attach-button__file-type" : "attach-button__file-type attach-button__file-type_disabled" }>.doc .txt .png .jpg</div> }
        </div>
        
    )
}

export default Attach