import React from "react"
import './Avatars.css'

const Avatars = props => {

    const getAvatar = () => {

        let userData = {}

        let xhr = new XMLHttpRequest()
        xhr.open('GET', 'https://randomuser.me/api/', false)
        xhr.send()

        if (xhr.status !== 200) {
            console.log( xhr.status + ': ' + xhr.statusText )
        } else {
            userData = JSON.parse(xhr.responseText)
            return userData.results[0].picture.thumbnail
        }
    }

    const avatarBody = (
        <img className={`profile-button__avatar profile-button__avatar_${props.size}`} alt="" src={getAvatar()}/>
    )
    const placeholder = (
        <div className={`profile-button__placeholder profile-button__placeholder_${props.size}`}>
            <div className={`avatar-placeholder__icon_${props.size}`}></div>
        </div>
    )
    
    return (
        props.hasAvatar ? avatarBody : placeholder
    )
}

export default Avatars