import React, { Component } from "react"
import './MenuItem.css'

export default class MenuItem extends Component {

    state = {
        isActive: this.props.isActive
    }

    hoverItemHandle = e => {
        if(!this.props.isActive) {
            this.setState({isActive: !this.state.isActive})
        }
    }
    
    render() {

        const menuItem = (
            <div className="menu__item" onMouseEnter={this.hoverItemHandle} onMouseLeave={this.hoverItemHandle}>
                <div className="menu__icon">
                    <div className={this.props.icon}></div>
                </div>
                <div className="menu__item-title">{this.props.title}</div>
            </div>
        )
        const menuItemActive = (
            <div className="menu__item menu__item_active" onMouseEnter={this.hoverItemHandle} onMouseLeave={this.hoverItemHandle}>
                <div className="menu__icon">
                    <div className={this.props.icon + " " + this.props.icon + "_active"}></div>
                </div>
                <div className="menu__item-title menu__item-title_active">{this.props.title}</div>
            </div>
        )

        return (
            this.state.isActive ? menuItemActive : menuItem
        )
    }
}