import React, { Component } from "react"
import './Buttons.css'

export default class Buttons extends Component {

    render() {
        return (
            <div style={{width: this.props.buttonWidth}} className={`${this.props.buttonType ? "action-button action-button_" + this.props.buttonType : "action-button action-button_blue-solid"}`}>
                <div className={`${this.props.buttonType ? "action-button__title action-button__title_" + this.props.buttonType : "action-button action-button_blue-solid"}`}>{this.props.text ? this.props.text : "Кнопка"}</div>
            </div>
        )
    }
}